/* https://www.w3schools.com/howto/tryit.asp?filename=tryhow_css_switch */

:root {
  --orange: rgb(252, 112, 5);
  --orange-thin: rgba(252, 112, 5, 0.671);
  --orange-dim: rgba(252, 112, 5, 0.397);
}

.switch {
    position: relative;
    display: inline-block;
    width: 64px;
    height: 34px;
    margin-right: 5%;
  }
  
  .switch input { 
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: rgba(252, 112, 5, 0.671);
    background-color: var(--orange-thin);
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px rgba(252, 112, 5, 0.671);
    box-shadow: 0 0 1px var(--orange-thin);
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    transform: translateX(26px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }
